const sizeImage=(imageselector)=>{
    const blocks=document.querySelectorAll(imageselector);
    function Sowimage(block){
        let img=block.querySelector('img');
        img.src = img.src.slice(0, -4) + '-1.png';
        
        block.querySelectorAll('p:not(.sizes-hit)').forEach(p =>{
            p.style.display='none';
        });
    }
    function hideImage(block){
        let img=block.querySelector('img');
        img.src = img.src.slice(0, -6) + '.png';
        block.querySelectorAll('p:not(.sizes-hit)').forEach(p =>{
            p.style.display='block';
        });
    }
    blocks.forEach(item =>{
        
        item.addEventListener('mouseover',function(){
            Sowimage(item);
            item.classList.add('animated', 'rotateInDownLeft');
        });
        item.addEventListener('mouseout',function(){
            hideImage(item);
            item.classList.remove('animated', 'rotateInDownLeft');
        });
    });

};
export default sizeImage;