const filter = () => {
    const menu = document.querySelector('.portfolio-menu'),
          items = menu.querySelectorAll('li'),
          btnAll = menu.querySelector('.all'),
          btnLovers = menu.querySelector('.lovers'),
          btnChef = menu.querySelector('.chef'),
          btnGirl = menu.querySelector('.girl'),
          btnGuy = menu.querySelector('.guy'),
          btnGrandmother = menu.querySelector('.grandmother'),
          btnGranddad = menu.querySelector('.granddad'),
          wrapper = document.querySelector('.portfolio-wrapper'),
          markAll = wrapper.querySelectorAll('.all'),
          markGirl = wrapper.querySelectorAll('.girl'),
          markLovers = wrapper.querySelectorAll('.lovers'),
          markChef = wrapper.querySelectorAll('.chef'),
          markGuy = wrapper.querySelectorAll('.guy'),
          no = document.querySelector('.portfolio-no');


          const Tybefolder = (marker) =>{
            markAll.forEach(marko =>{
                marko.style.display='none';
                marko.classList.remove('animated');
                marko.classList.remove('fadeIn');
                
            });

            if(marker){
              marker.forEach(mark => {
                mark.style.display = 'block';
                mark.classList.add('animated', 'fadeIn');
            });
            }else{
              no.style.display='block';
              
            }

          };

  

          btnAll.addEventListener('click',function(){
            Tybefolder(markAll);
          });
          
          btnLovers.addEventListener('click',function(){
            Tybefolder(markLovers);
          });
          
          btnChef.addEventListener('click',function(){
            Tybefolder(markChef);
          });
          
          btnGirl.addEventListener('click',function(){
            Tybefolder(markGirl);
          });
          
          btnGuy.addEventListener('click',function(){
            Tybefolder(markGuy);
          });
          btnGranddad.addEventListener('click',function(){
            Tybefolder();
          });
          btnGrandmother.addEventListener('click',function(){
            Tybefolder();
          });
             menu.addEventListener('click',function(e){
              let target=e.target;
              if(target && target.tagName=='LI'){
                items.forEach(item =>item.classList.remove('active')
                );
                target.classList.add('active');
              }
             });
        };
        
        export default filter;